/*
    Cube animation
*/
const add = (el) => {
    el.classList.add('active')
}
const remove = (el) => {
    el.classList.remove('active')
}
sr.reveal('figure.cube > img', {
    beforeReset: remove,
    beforeReveal: add
})

sr.reveal('figure.cube', {
    origin: 'top',
    distance: '30px',
    delay: 300
});



for (let i = 100; i < 1000; i += 100) {
    sr.reveal('.delay2-' + i, {
        delay: i,
    });
}



